.card {
  position: relative;
  background-color: aliceblue;
  width: 600px;
  padding: 20px;
  max-width: 85vw;
  height: 50vh;
  box-shadow: 0px 19px 53px 0px rgba(63, 77, 90, 0.4);
  border-radius: 22px;
  background-size: cover;
  background-position: center;
}
.cards__cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
}
.swipe {
  position: absolute;
}
.cardContent {
  width: 100%;
  height: 100%;
}
.card h3 {
  position: relative;
  bottom: 0;
  margin: 10px;
  color: azure;
}
