img {
  width: 149px;
  display: flex;
}

div.buttons {
  display: flex;

  justify-content: space-evenly;
  position: fixed;
  width: 100%;
  bottom: 0vh;
}
.star img,
.flashing img {
  height: 90px;
  width: 100px;
}
.buttons .MuiIconButton-root {
  background-color: white;
  box-shadow: 0px 18px 54px 3px rgba(88, 88, 88, 0.6);
}
.flashing img {
  width: 120px;
}
.fav img,
.repat img,
.close img {
  width: 136px;
}

.repeat_icon {
  color: #f5b748 !important;
}
.close_icon {
  color: #ec5e6f !important;
}
.star_icon {
  color: #62b4f9 !important;
}
.fav_icon {
  color: #76e2b3 !important;
}
.flash_icon {
  color: #915dd1 !important;
}
